.company{
	&-header{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		padding-bottom: 50px;
		> div{
			width: 30%;
		}
		&__logo{
			img{
				width: 100%;
				margin-bottom: 30px;
			}
			p{
				font-size: 27px;
				line-height: 150%;
				color: #000000;
			}
		}
		&__img{
			width: 36.66% !important;
			img{
				width: 100%;
			}
		}
		&__list{
			padding-left: 30px;
			li{
				padding-left: 70px;
				background-position: left center;
				background-repeat: no-repeat;
				background-size: 40px;
				min-height: 60px;
				margin-bottom: 20px;
				font-size: 16px;
				line-height: 140%;
				color: #000000;
				display: flex;
				align-items: center;

			}
		}
	}
	&-certificate{
		background-color: #F1F5F6;
		padding: 120px 0;
		.container{
			display: flex;
			align-items: center;
		}
		&__zag{
			width: 500px;
			span{
				font-size: 26px;
				line-height: 169%;
				color: #F74B4B;
				margin-bottom: 16px;
				display: block;

			}
			li{
				margin-bottom: 34px;
				font-weight: bold;
				font-size: 70px;
				line-height: 53px;
				color: #CACFD0;
			}
		}
		&__slide{
			width: calc(100% - 500px);
			.certificate-gallery__container a{
				height: 300px;
				img{
					width: auto;
					max-height: 100%;
				}

			}
			.certificate-gallery__container::before{
				background: linear-gradient(to right, #f1f5f6 0%, #f1f5f6 20%, rgba(255, 255, 255, 0) 100%);
			}
			.certificate-gallery__container::after{
				background: linear-gradient(to left, #f1f5f6 0%, #f1f5f6 20%, rgba(255, 255, 255, 0) 100%);
			}
		}
	}
	&-text{
		display: flex;
		justify-content: space-between;
		padding: 100px 0 30px;
		img{
			border-radius: 15px;
			margin-bottom: 30px;
		}
		p{
			line-height: 160%;
			letter-spacing: -0.02em;
			color: #414141;
			margin-bottom: 20px;
			a{
				font-weight: 600;
				text-decoration: underline;
			}
		}
		.h3{
			font-weight: 600;
			font-size: 22px;
			margin-bottom: 20px;
		}
		&__main{
			width: 61%;
		}
		&__right{
			width: calc(39% - 35px);
			.h3{
				color: #E63728;
			}
		}
	}
	&-contact{
		padding: 70px 0;
		.h2{
			font-size: 36px;
			line-height: 122%;
			text-align: center;
			color: #232323;
			margin-bottom: 60px;
		}
		&__list{
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			&-finance{
				width: 33.33%;
				li{
					font-size: 16px;
					line-height:156%;
					color: #000000;
					margin-bottom: 5px;
				}
			}
			&-info{
				width: 66.66%;
				ul{
					display: flex;
					flex-wrap: wrap;
				}
				li{
					font-size: 16px;
					line-height: 156%;
					letter-spacing: -0.02em;
					color: #363636;
					margin-bottom: 40px;
					padding-bottom: 40px;
					padding-left: 65px;
					background-repeat: no-repeat;
					background-position: left top;
					background-size: 40px;
					width: 50%;
					.red{
						display: block;
						font-weight: 600;
						font-size: 18px;
						line-height: 120%;
						color: #EF4949;
						margin-bottom: 5px;
					}
				}
			}
		}
	}
	&-brands{
		.brands{
			background-color: #fff;
		}
	}
}